const components = [															// 
	require('&/base').default,													// 
	require('&/data').default,													// 
	require('&/form').default,													// 
	require('&/layout').default,												// 
];

const install = function (Vue) {												//  install  Vue 
	if (install.installed) return												// 
	install.installed = true

	Vue.prototype.$color = darkMode();											// 

	let componentsList = new Array;
	components.map(component => {
		componentsList = componentsList.concat(component.components);
	});

	componentsList.map(component => {
		component.install(Vue);
	});
}

function darkMode(color) {
	if(!color){
		let now = new Date(),hour = now.getHours();
		if(hour > 6 && hour < 19){
			return 'light';														// 									
		}else {
			return 'dark';
		}
	}else return color;
}

if (typeof window !== 'undefined' && window.Vue) {								//  Vue  Vue 
	install(window.Vue)
}

export default {
	install,
	//  install Vue.use()
	...components
}