<template>
<div :class="[viewHandleState ? 'viewHandleOpen' : '', direction ? 'row' : 'column']" 
	:style="viewHandleState ? '' : distance" class="viewHandle" 
	@mousemove.stop="viewHandle" 
	@mousedown.stop="viewHandleState = true"
	@mouseup.stop="viewHandleState = false"
	@mouseout.stop="viewHandleState = false"/>
</template>

<script>
export default {
	props: {
		value: {																		// ,
			type: Number,
			default: 0
		},
		valueOpposite: {																//  
			type: Number,
			default: 0
		},
		limits: {																		//  
			type: Number,
			default: 10
		},
		offsetDirection: {																//  true false
			type: Boolean,
			default: true
		},
		direction: {																	//  true false
			type: Boolean,
			default: true
		}
	},
	data() {
        return {
			viewHandleState: false,														// 
        }
	},
	computed: {
		distance() {																	// 
			return (this.direction) ? {[this.offsetDirection ? 'left' : 'right']: `${this.value-4}px`} : {[this.offsetDirection ? 'top' : 'bottom']: `${this.value-4}px`}
		},

		viewSize() {																	// 
			return this.direction ? this.$el.clientWidth : this.$el.clientHeight;											// ,
		}
	},
	methods: {
		// 
		viewHandle(event) {
			if(this.viewHandleState){													// 
				if(this.direction)														// 
					if(this.offsetDirection)											// 
					this.$emit("input", this.viewHandleXLimits(event.offsetX));
					else
					this.$emit("input", this.viewHandleXLimits(this.viewSize - event.offsetX));
				else
					if(this.offsetDirection)
					this.$emit("input", this.viewHandleXLimits(event.offsetY));
					else
					this.$emit("input", this.viewHandleXLimits(this.viewSize - event.offsetY));
			}
		},
		// 
		viewHandleXLimits(value) {
			let min = this.limits,
				max = this.viewSize-(this.limits+this.valueOpposite);
			if(value <= min)
				return min;
			else if(value >= max)
				return max;
			else return value;
		},
	},
	// beforeMount() {
	// 	this.$el.ondragstart = (event) => event.preventDefault();
	// }
}
</script>

<style lang="less" scoped>
@import '../../../_style/variables.less';

/*  */
.viewHandle {
	// bottom: 0px;
	&:hover {
		background-color: rgba(75, 75, 75, 0.2);
	}
}
.row {
	width: 8px;
	height: 100%;
	cursor: w-resize;
	position: absolute;
	top: 0;
}
.column {
	width: 100%;
	height: 8px;
	cursor: n-resize;
	position: absolute;
	left: 0;
}

// 
.viewHandleOpen {
	width: 100%;
	height: 100%;
	// position: relative;
	position: absolute;
	left: 0;
	top: 0;
	
	// background-color: aqua;
	opacity: 0;
	user-select: none;
	z-index: 1;
}
</style>