<template>
<div :class="colClass">
<slot/>
</div>
</template>

<script>
export default {
	name: 'ButCol',
	props: {
		col: {
			type: Object,
			default: function () {
				return {
					// xs: 1,
					// sm: 1,
					// md: 1,
					// lg: 1
				}
			}
		},
		
	},
	data() {
		return {

		}
	},
	computed: {
		colClass() {
			let colClass = ''
			for(let cols in this.col){
				colClass += cols + '-' + this.col[cols] + ' ';
			}
			return colClass
		}
	},
	methods: {

	}
}
</script>

<style lang="less" scoped>
@import '../../../_style/variables.less';
// 
//
// @ grid-columnsBootstrap

@grid-columns:              12;															//** .
@grid-gutter-width:         30px;														//** ,.
// @grid-float-breakpoint:     @screen-sm-min;
//** Point at which the navbar begins collapsing.
// @grid-float-breakpoint-max: (@grid-float-breakpoint - 1);


/*  */
.cont(@index) when (@index > 0) {
	.cont((@index - 1));
	.xs-@{index} {
		float: left;
		width: percentage((@index / @grid-columns));
	}
	.sm-offset-@{index} {
		margin-left: percentage((@index / @grid-columns));
	}
	@media (min-width: @screen-sm) {										/*768px*/
		.sm-@{index} {
			float: left;
			width: percentage((@index / @grid-columns));
		}
		.sm-offset-@{index} {
			margin-left: percentage((@index / @grid-columns));
		}
	}
	@media (min-width: @screen-md) {										/*992px*/
		.md-@{index} {
			float: left;
			width: percentage((@index / @grid-columns));
		}
		.md-offset-@{index} {
			margin-left: percentage((@index / @grid-columns));
		}
	}
	@media (min-width: @screen-lg) {										/*1200px*/
		.lg-@{index} {
			float: left;
			width: percentage((@index / @grid-columns));
		}
		.lg-offset-@{index} {
			margin-left: percentage((@index / @grid-columns));
		}
	}

}
.cont(@grid-columns);

</style>