<template>
<div :class="size">
	<div class="infoRow">
        <img @click='toggle' v-if="data.hasOwnProperty(children) && data[children].length > 0" :class="[ open ? 'expand' : 'folded']" class="arrow" src="../arrow.svg"/>
		<img v-if="icon.iconKey" class="icon" :src="iconFilter(data)"/>
        <span @click='eventBroadcasting' class="name">{{data[nameKey]}}</span>
    </div>
	<!-- <transition name="fade"> -->
	<but-collapse>
	<div v-show='open' class="foldedBar">
		<but-tree v-for="(cel, index) in data[children]" :key="index" 
			:data='cel'
			:children="children"
			:nameKey="nameKey"
			:size="size"
			:icon="icon"
			@node-event="eventPassed"/> 
	</div>
	</but-collapse>
	<!-- </transition> -->
</div>
</template>
<script type="text/javascript">
export default {
    name: 'ButTree',
    props: {
		data: {
			value: Object,
			default() {
				return new Object;
			}
		},
		children: {
			value: String,
			default: 'children'
		},
		nameKey: {
			value: String,
			default: 'name'
		},
		icon: {
			value: Object,
			default() {
				return {
					iconKey: '',
					iconFilter: {
						// icon: ''
					}
				};
			}
		},
		size: {
			value: String,
			default: 'small'
		}
	},
    data() {
        return {
            open: 0
        }
	},
	computed: {
		color() {
			return this.$color;
		}
	},
    methods: {
        toggle() {
            this.open ^= 1;                                                         // 
		},
		iconFilter(data) {
			let key = data[this.icon.iconKey];
			if(key)
				return this.icon.iconFilter[key];
			else
				return 'ata:image/svg+xml;base64,';
			// console.log(this.icon);
			// return this.icon.iconFilter[this.data[this.icon.iconKey]];
		},
		eventBroadcasting(event) {
			this.$emit('node-event', this.data, event);
		},

		// -
		eventPassed(node, event) {
			this.$emit('node-event', node, event);
		},
	},
	components: {
		butCollapse: require("&/base/collapse/src/main").default
	}
}
</script>
<style lang="less" scoped>
@min: 24px;
@max: 30px;

/*  */
.objectBar(@size) {
	margin: @size/5 0;

	// 
	.infoRow {
		display: flex;
		justify-content: flex-start;
		height: @size;
		padding-left: @size;
		user-select: none;

		// 
		.arrow {
			width: @size;
			margin-left: -@size;
			padding: .2 * @size;
			box-sizing: border-box;
			cursor: pointer;
		}
		.icon {
			width: @size;
			cursor: pointer;
		}
		.name {
			height: @size;
			// flex-grow: 1;
			display: flex;
			flex-direction: column;
			justify-content: center;
			font-size: 0.8 * @size;
			margin-left: @size/4;
			cursor: pointer;
			transition: .1s ease-out;
			&:active {																	// 
				transition: .1s ease-out;
				opacity: 0.5;
			}
		}

		// 
		.expand {
			transition: .3s;
			transform: rotate(90deg);
			filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=1);
		}
		.folded {
			transition: .3s;
			transform: rotate(0deg);
		}
	}
	
	.foldedBar {
		margin-left: 2rem;
		flex-shrink: 0;
	}
}

// 
.medium {
	.objectBar(@max);
}
.small {
	.objectBar(@min);
}
</style>
